import React from 'react';
import APILayout from '../../../../layouts/APILayout';
import Section from '../../../../modules/apidoc/components/Section';
import FunctionName from '../../../../modules/apidoc/components/FunctionName';
import ParameterTable from '../../../../modules/apidoc/components/ParameterTable';
import Parameter from '../../../../modules/apidoc/components/Parameter';
import { MapType, VectorSourceType, DrawInteractionType } from '../../../../modules/apidoc/components/OpenlayersDataTypes';
import ParameterGroup from '../../../../modules/apidoc/components/ParameterGroup';
import Highlighted from '../../../../modules/apidoc/components/Highlighted';
import { InfoAlert } from '../../../../modules/apidoc/components/Alerts';
import FunctionDescription from '../../../../modules/apidoc/components/FunctionDescription';
import Link from '../../../../modules/apidoc/components/Link';

const OpenlayersDrawInteractionDocLink = ({ children }) => <Link href="https://openlayers.org/en/latest/apidoc/module-ol_interaction_Draw-Draw.html">{children || 'https://openlayers.org/en/latest/apidoc/module-ol_interaction_Draw-Draw.html'}</Link>;
const OpenlayersSnapInteractionDocLink = ({ children }) => <Link href="https://openlayers.org/en/latest/apidoc/module-ol_interaction_Snap-Snap.html">{children || 'https://openlayers.org/en/latest/apidoc/module-ol_interaction_Snap-Snap.html'}</Link>;

export default () => (
    <APILayout>
        <h4 className='font-weight-bold mb-4'>easyolmaps/vector/Draw <small><span className='ml-2 bg-warning'>
            ¡Introduced in replace of <strong>Drawing</strong>!
            </span></small></h4>
        <Section>
            <FunctionName parameters={['options', 'snapOptions']}>new Draw</FunctionName>
            <ParameterTable>
                <ParameterGroup name='options' type='Draw Options'>
                    <ParameterTable name='options' withDefaultColumn={true}>
                        <Parameter name='shape' type='string' defaultValue=' '>Shape to draw. It can be one of the following: <Highlighted>Point</Highlighted>, <Highlighted>Polygon</Highlighted>, <Highlighted>Line</Highlighted>, <Highlighted>Section</Highlighted>, <Highlighted>Box</Highlighted>, <Highlighted>Circle</Highlighted>.</Parameter>
                        <Parameter name='source' type={<VectorSourceType />} defaultValue=' ' >Destination source for the drawn features.</Parameter>
                        <Parameter name='snapping' type={'Boolean'} defaultValue='false' >Enable snapping.</Parameter>
                    </ParameterTable>
                    <InfoAlert>You can add other options of the <strong><OpenlayersDrawInteractionDocLink>Draw Interaction</OpenlayersDrawInteractionDocLink></strong>.</InfoAlert>
                </ParameterGroup>
                <Parameter name='snapOptions' type='object'>Same parameters to create a <OpenlayersSnapInteractionDocLink>Snap Interaction</OpenlayersSnapInteractionDocLink>.</Parameter>
            </ParameterTable>
            <strong>Extends:</strong>
            <ul>
                <li><DrawInteractionType /></li>
            </ul>
        </Section>
        <Section>
            <FunctionName parameters={['map']}>start</FunctionName>
            <FunctionDescription>Start drawing. Calling this function will add the interaction to the <Highlighted>map</Highlighted>.</FunctionDescription>
            <ParameterTable>
                <Parameter name='map' type={<MapType />}>The Map.</Parameter>
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName>finish</FunctionName>
            <FunctionDescription>Finish drawing. Calling this functions will remove the interaction.</FunctionDescription>
        </Section>
    </APILayout>
)